import React, { Component } from 'react'
import { Route, Switch } from 'react-router-dom'
import Create from './pages/Create'
import List from './pages/List'
import Task from './pages/Task'

export class Main extends Component {
  render() {
    return (
      <div>
        <Switch>
          <Route path="/list">
            <List />
          </Route>
          <Route path="/create">
            <Create />
          </Route>
          <Route path="/task/:id">
            <Task />
          </Route>
        </Switch>
      </div>
    )
  }
}

export default Main
