import initial from "./initial";
import { ADD_TASK, CHANGE_TASK, COMPLETE_TASK, CREATE_TASK } from "./types";

function rootReducer(state = initial, action) {
  let copy;
  switch (action.type) {
    case ADD_TASK:
      return [
        ...state,
        action.payload
      ];
    case COMPLETE_TASK:
      copy = [...state];
      copy[action.payload.id].completed = true;
      return copy;
    case CREATE_TASK:
      const task = {...action.payload};
      task.id = state.length;
      return [
        ...state,
        task
      ];
    case CHANGE_TASK:
      const { id, newTask } = action.payload;
      console.log(id, newTask);
      copy = [...state];
      copy[id] = newTask;
      return copy;
  }
  return state;
}

export default rootReducer;
