import { ADD_TASK, CHANGE_TASK, COMPLETE_TASK, CREATE_TASK } from './types';

export function addTask(payload) {
  return {
    type: ADD_TASK,
    payload
  };
}

export function completeTask(payload) {
  return {
    type: COMPLETE_TASK,
    payload
  }
}

export function createTask(payload) {
  return {
    type: CREATE_TASK,
    payload
  };
}

export function changeTask(payload) {
  return {
    type: CHANGE_TASK,
    payload
  };
}
