import React, { Component } from 'react'
import { Link } from 'react-router-dom';

export class Task extends Component {
  render() {
    const { title, date, desc, completed, id } = this.props;

    return (
      <tr>
        <td>
          {Number(id) + 1}
        </td>
        <td className="">
          {title}
        </td>
        <td className="text-default">
          {date}
        </td>
        <td
          style={{
            whiteSpace: 'pre',
            maxWidth: '500px',
            overflow: 'hidden'
          }}
          className=""
        >
          {desc}
        </td>
        <td className={completed ? 'text-success' : 'text-warning'}>
          {completed ? 'Completed' : 'Active'}
        </td>

        <td>
          <Link className="btn btn-success" to={`/task/${id}`}>
            Open
          </Link>
        </td>
      </tr>
    )
  }
}

export default Task
