import React, { Component } from 'react'
import { connect } from 'react-redux'
import Task from './Task';
import moment from 'moment';

export class Tasks extends Component {
  render() {
    const tasks = this.props.tasks;
    let filtered;
    switch (this.props.filterType) {
      case 'all':
        filtered = tasks;
        break;
      case 'status':
        filtered = tasks.filter(task => {
          if (task.completed) {
            return true;
          }
          return false;
        });
        console.log(filtered);
        break;
      case 'deadline':
        filtered = tasks.filter(task => {
          if (moment(task.date).isAfter(moment())) {
            return true;
          }
          return false;
        });
        break;
    }

    const renderedTasks = filtered.map(task => (
      <Task
        id={task.id}
        title={task.title}
        date={task.date}
        desc={task.desc}
        completed={task.completed}
      />
    ));

    return (
      <table className="container-fluid table table-hover table-bordered table-sm">
        <thead>
          <tr style={{ color: 'black', fontWeight: 'bold' }}>
            <td>#</td>
            <td>Title</td>
            <td>Date</td>
            <td>Desc</td>
            <td>Status</td>
            <td>Open</td>
          </tr>
        </thead>
        <tbody>
          {renderedTasks}
        </tbody>
      </table>
    )
  }
}

const mapStateToProps = (state) => ({
  tasks: state
})

const mapDispatchToProps = {
  
}

export default connect(mapStateToProps, mapDispatchToProps)(Tasks)

