import { Field, Form, Formik } from 'formik';
import React, { Component } from 'react'
import { connect } from 'react-redux'
import { withRouter } from 'react-router'
import { completeTask, changeTask } from 'store/actions'

export class Task extends Component {
  completeTask = () => {
    this.props.complete({ id: this.task.id });
    console.log(this.props.history);
  };

  changeTask = (v) => {
    const id = this.task.id;
    const newItem = {...v};
    this.props.change({ id, newItem })
  };

  render() {
    const { id } = this.props.match.params;
    this.task = this.props.tasks[id];

    return (
      <div className="container-md mt-5">
        <h1>Task {Number(id) + 1}</h1>
        <Formik
          initialValues={{
            title: this.task.title,
            desc: this.task.desc,
            date: this.task.date,
            tags: this.task.tags.join(' ')
          }}
          onSubmit={(v) => {
            const values = {
              title: v.title,
              desc: v.desc,
              date: v.date,
              tags: v.tags.split(' '),
              completed: this.task.completed
            }
            console.log(values);
            this.changeTask(values);
          }}
        >
          <Form>
            <div className="form-group">
              <label htmlFor="title">Title:</label>
              <Field
                id="title"
                name="title"
                className="form-control"
              />
            </div>
            <div className="form-group">
              <label htmlFor="desc">
                Description:
              </label>
              <Field
                id="desc"
                as="textarea"
                name="desc"
                className="form-control"
              />
            </div>
            <div className="form-group">
              <label htmlFor="date">
                Date:
              </label>
              <Field
                id="date"
                type="date"
                name="date"
                className="form-control"
              />
            </div>
            <div className="form-group">
              <label htmlFor="tags">
                Tags: {this.task.tags.map(tag => (
                  <span className="badge-pill badge badge-secondary">
                    {tag}
                  </span>
                ))}
              </label>
              <Field
                id="tags"
                name="tags"
                className="form-control"
              />
            </div>
            <button
              type="submit"
              className="btn btn-warning"
            >
              Submit Changes
            </button> <button
              className="btn btn-success"
              onClick={this.completeTask}
            >
              Make completed
            </button>
          </Form>
        </Formik>
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
  tasks: state
});

const mapDispatchToProps = (dispatch) => {
  return {
    complete: (payload) => dispatch(completeTask(payload)),
    change: (payload) => dispatch(changeTask(payload))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Task))
